body::after {
    content: "";
    background-color: rgba(5, 5, 5, 0.6);
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
    transition: opacity 0.3s ease 0s;
    pointer-events: none;
    z-index: 149;
}
.popup-show body::after {
    opacity: 1;
}
.popup {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    padding: 30px 10px;
    transition: visibility 0.8s ease 0s;
    visibility: hidden;
    pointer-events: none;
    @media (max-width: 768px) {
        margin: 70px 0px 0px 0px;
        padding: 0px;
    }
}
.popup_show {
    z-index: 150;
    visibility: visible;
    overflow: auto;
    pointer-events: auto;
}
.popup_show .popup__content {
    visibility: visible;
    transform: scale(1);
}
.popup__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100%;
    flex: 1 1 auto;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: 768px) {
        justify-content: flex-end;
    }
}
.popup__content {
    visibility: hidden;
    transform: scale(0);
    transition: transform 0.3s ease 0s;
    background: var(--color-base);
    width: 100%;
    max-width: 700px;
    border-radius: 8px;
    min-height: 375px;
    @media (max-width: 768px) {
        width: 100%;
        max-width: 100%;
        border-radius: 8px 8px 0px 0px;
    }
}

.popup__close {
    position: absolute;
    top: -5px;
    right: -20px;
    background: transparent;
    transition: opacity 0.3s;
    &:hover {
        opacity: 0.8;
    }
    @media (max-width: 768px) {
        display: none;
    }
}

