.popup-share-thoughts {
    padding: 20px 20px 25px 20px;
    display: flex;
    flex-direction: column;
    min-height: 375px;
    @media (max-width: 768px) {
        padding: 67px 10px 10px 10px;
        min-height: 100%;
        height: 100%;
    }
    // .popup-share-thoughts__top

    &__top {
        display: flex;
        align-items: center;
        margin: 0px 0px 16px 0px;
        width: 100%;
    }

    // .popup-share-thoughts__image

    &__image {
        flex: 0 0 40px;
        margin: 0px 10px 0px 0px;
        img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
        }
    }

    &__comment {
        display: flex;
        flex-direction: column;
        align-items: start;
    }

    // .popup-share-thoughts__name

    &__name {
        font-weight: 700;
        font-size: 16px;
        line-height: 169%;
        color: var(--color-red);
    }

    // .popup-share-thoughts__textarea

    &__textarea {
        width: 100%;
        flex: 1 1 auto;
        textarea {
            padding: 12px 20px 16px 74px;
            width: 100%;
            resize: none;
            font-weight: 400;
            font-size: 15px;
            line-height: 135%;
            min-height: 52px;
            max-height: 611px;
            background-color: var(--color-base);
            overflow: auto;
            -ms-overflow-style: none;
            scrollbar-width: none;
            &::-webkit-scrollbar {
                display: none;
            }
            &::placeholder {
                color: var(--color-grey);
            }
            @media (max-width: 768px) {
                max-height: 313px;
                font-size: 12px;
                padding: 16px 20px;
            }
        }
    }

    // .popup-share-thoughts__bottom

    &__bottom {
        width: 100%;
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        height: 100%;
        border-top: 1px solid var(--color-grey);
        margin: 20px 0px 0px 0px;
        padding: 20px 0px 0px 0px;
        @media (max-width: 768px) {
            height: auto;
        }
    }

    // .popup-share-thoughts__files

    &__files {
        display: flex;
        align-items: center;
        gap: 20px;
    }

    // .popup-share-thoughts__file

    &__file {
        position: relative;
        input {
            position: absolute;
            opacity: 0;
            width: 100%;
            height: 100%;
        }
    }

    // .popup-share-thoughts__buttons

    &__buttons {
        display: flex;
        gap: 20px;
        width: 100%;
        justify-content: flex-end;
    }

    // .popup-share-thoughts__public

    &__public {
        &.disabled {
            color: #adadd8;
            background-color: var(--color-blue-grey);
            pointer-events: none;
            border: 1px solid transparent;
        }
    }

    // .popup-share-thoughts__cancel

    &__cancel {
        svg {
            display: none;
        }
        @media (max-width: 768px) {
            position: absolute;
            top: 16px;
            left: 10px;
            border-radius: 4px;
            background-color: var(--color-blue-grey);
            border: 1px solid transparent;
            width: 24px;
            height: 24px;
            padding: 0px 0px 0px 0px;
            z-index: 15;
            span {
                display: none;
            }
            svg {
                display: block;
                transform: rotate(90deg);
            }
            &:hover {
                svg {
                    fill: var(--color-red);
                }
            }
        }
    }

    // .popup-share-thoughts__clear

    &__clear {
        border: 1px solid transparent;
        background-color: var(--color-blue-grey);
        color: var(--color-red);
        &.disabled {
            color: #adadd8;
            background-color: var(--color-blue-grey);
            pointer-events: none;
        }
        @media (max-width: 768px) {
            position: absolute;
            top: 10px;
            right: 10px;
            z-index: 15;
        }
    }

    // .popup-share-thoughts__error

    &__error {
        position: fixed;
        right: 50px;
        bottom: 50px;
        display: flex;
        align-items: center;
        font-weight: 400;
        font-size: 16px;
        background: var(--color-base);
        border-radius: 10px;
        padding: 10px;
        width: 458px;
        padding: 10px;
        z-index: 25;
        opacity: 0;
        pointer-events: none;
        transition: opacity 0.3s;
        img {
            width: 24px;
            height: 24px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0px 10px 0px 0px;
        }
        &._active {
            opacity: 1;
            pointer-events: auto;
        }
        &.desct {
            @media (max-width: 992px) {
                display: none;
                &._active {
                    display: none;
                }
            }
        }
        &.mob {
            @media (min-width: 992px) {
                display: none;
            }
        }
        @media (max-width: 1200px) {
            right: 20px;
            bottom: 20px;
        }
        @media (max-width: 992px) {
            display: none;
            width: auto;
            position: relative;
            right: 0;
            bottom: 0;
            margin: 20px 0px 0px 50px;
            box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.1);
            &._active {
                display: flex;
            }
        }
        @media (max-width: 768px) {
            margin: 10px 0px 0px 0px;
        }
    }
}
#share-thoughts {
    @media (max-width: 768px) {
        margin: 0px 0px 0px 0px;
    }
    .popup__content {
        @media (max-width: 768px) {
            min-height: 100%;
            border-radius: 0px;
        }
    }
    .popup__wrapper {
        @media (max-width: 768px) {
            height: 100%;
        }
    }
}
.previews {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: 20px 0px 0px 50px;
    margin: 20px 0px 0px 0px;
    border-top: 1px solid var(--color-grey);
    gap: 10px;
    display: none;
    &._active {
        display: grid;
    }
    @media (max-width: 768px) {
        padding: 10px 0px 0px 0px;
        margin: 10px 0px 0px 0px;
    }
    .preview-item {
        position: relative;
        width: 100%;
        height: 145px;
        border-radius: 10px;
        overflow: hidden;
        &:hover {
            .preview-remove {
                opacity: 1;
                pointer-events: auto;
            }
        }
        .textarea-height & {
            height: 70px;
            @media (max-width: 480px) {
                height: 60px;
            }
        }
        @media (max-width: 768px) {
            height: 90px;
        }
    }
    .preview-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    .preview-remove {
        @media (max-width: 768px) {
            width: 16px;
            height: 16px;
            border-radius: 4px;
            svg {
                width: 12px;
                height: 12px;
            }
        }
    }
}

.citation {
    position: absolute;
    top: 22px;
    right: 56px;
    display: flex;
    align-items: start;
    @media (max-width: 992px) {
        right: 16px;
        top: 18px;
    }
    p {
        background: #f2f2f2;
        border-radius: 50px;
        padding: 10px 20px;
        font-weight: 400;
        font-size: 13px;
        line-height: 123%;
        margin: 12px 0px 0px 0px;
        @media (max-width: 992px) {
            padding: 6px 10px;
        }
    }
    // .citation__close

    &__close {
        cursor: pointer;
        svg {
            fill: var(--color-black);
        }
    }
}

#edit {
    @media (max-width: 768px) {
        margin: 0px 0px 0px 0px;
    }
    .popup__content {
        @media (max-width: 768px) {
            min-height: 100%;
            border-radius: 0px;
        }
    }
}

#comment {
    .popup__content {
        max-width: 908px;
    }
    .popup-share-thoughts {
        display: flex;
        flex-direction: column;
        gap: 30px;
        padding: 30px 50px;
        @media (max-width: 992px) {
            padding: 68px 10px 10px 10px;
        }
    }
    .popup-share-thoughts__name {
        color: var(--color-black);
    }
    .post__content {
        position: relative;
        border-bottom: none;
        padding: 20px 0px 0px 0px;
        margin: 0px 0px 0px 50px;
        @media (max-width: 992px) {
            margin: 0px 0px 0px 0px;
        }
    }
    .post__slider {
        @media (max-width: 992px) {
            padding: 0px 0px 0px 0px;
            margin: 0px 0px 0px 0px;
            width: 100%;
        }
        @media (max-width: 768px) {
            padding: 0px 10px;
            margin: 0px -10px;
            width: calc(100% + 20px);
        }
    }
    .popup-share-thoughts__bottom {
        border-top: none;
        padding: 0px 0px 0px 0px;
        margin: 0px 0px 10px 0px;
    }
    .post__line {
        display: block;
        top: 20px;
        left: -28px;
        height: calc(100% - 20px);
    }
    .popup-share-thoughts__cancel {
        @media (max-width: 768px) {
            position: relative;
            top: 0;
            right: 0;
            border-radius: 9px;
            background-color: transparent;
            border: 1px solid var(--color-black);
            width: auto;
            height: auto;
            padding: 10px 24px;
            z-index: 15;
            left: 0;
            span {
                display: block;
            }
            svg {
                display: none;
            }
        }
    }
    .popup-share-thoughts__buttons {
        @media (max-width: 768px) {
            margin: 0px 0px 0px 0px;
        }
