    &__columns {
        display: flex;
        flex-direction: column;
        max-height: 400px;
        overflow: auto;
        -ms-overflow-style: none;
        scrollbar-width: none;
        &::-webkit-scrollbar {
            display: none;
        }
    }

    // .popup-settings-profile__column

    &__column {
        margin: 0px 0px 20px 0px;
        display: flex;
        justify-content: space-between;
        gap: 20px;
        &:last-child {
            margin: 0px 0px 0px 0px;
        }
        .post__header {
            flex: 1 1 auto;
            display: flex;
            justify-content: space-between;
            margin: 0px 0px 0px 0px;
            gap: 4px 10px;
            min-width: 0;
            @media (max-width: 550px) {
                min-width: 0;
                flex-direction: column;
                justify-content: start;
            }
        }
        .post__left {
            flex: 1 1 auto;
        }
    }

    // .popup-settings-profile__right

    &__right {
        flex: 0 0 160px;
        font-weight: 500;
        font-size: 14px;
