.popup-settings-profile3 {
    padding: 20px 10px;
    margin: 0px 0px 0px 0px;
    .popup__content {
        min-height: 100%;
        max-width: 700px;
        border-radius: 20px;
        padding: 30px;
        @media (max-width: 768px) {
            padding: 20px;
        }
    }
    .popup__wrapper {
        justify-content: center;
    }
    .popup__close {
        top: 16px;
        right: 16px;
        width: 24px;
        height: 24px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    // .popup-settings-profile3__content

    &__content {
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

    // .popup-settings-profile3__top

    &__top {
        display: flex;
        flex-direction: column;
        gap: 10px;

        p {
            font-weight: 500;
            font-size: 16px;
            line-height: 135%;
        }
    }

    // .popup-settings-profile3__title

    &__title {
        font-weight: 500;
        font-size: 24px;
        line-height: 120%;
        padding: 0px 30px 0px 0px;
    }

    // .popup-settings-profile3__body

    &__body {
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

    // .popup-settings-profile3__column

    &__column {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    // .popup-settings-profile3__name

    &__name {
        font-weight: 500;
        font-size: 20px;
        line-height: 135%;
    }

    // .popup-settings-profile3__inputs

    &__inputs {
        display: flex;
        flex-direction: column;
        gap: 10px;
        .select1 {
            flex: 0 1 auto;
            width: 100%;
            z-index: 1;
            &.active {
                z-index: 50;
                .select1__title {
                    border: 1px solid var(--color-red);
                    background-color: transparent;
                    svg {
                        transform: rotate(-180deg);
                    }
                }
                .select1__options {
                    border: 1px solid var(--color-red);
                }
            }
        }
        .select1__title {
            border: 1px solid var(--color-grey);
            border-radius: 10px;
            width: 100%;
            max-width: 100%;
            height: 39px;
            padding: 5px 18px 5px 12px;
            font-size: 16px;
            span {
                display: block;
            }
            svg {
                transition: transform 0.3s;
            }
            &:hover {
                background-color: transparent;
            }
        }
        .select1__options {
            display: flex;
            flex-direction: column;
            gap: 0px;
            padding: 10px;
            width: 100%;
            .options__label {
                padding: 0px 0px 0px 0px;
                min-height: 100%;
                &:hover {
                    background-color: transparent;
                    .options__text {
                        color: var(--color-red);
                    }
                }
            }
            .options__item {
                display: flex;
                margin: 0px 0px 7px 0px;
                &:last-child {
                    margin: 0px 0px 0px 0px;
                }
            }
            .options__text {
                transition: color 0.3s;
                font-size: 16px;
            }
        }
        .select1__simplebar {
            max-height: 279px;
            @media (max-width: 768px) {
                max-height: 225px;
            }
        }
        .input {
            background: var(--color-base);
            height: 43px;
            border: 1px solid var(--color-grey);
            border-radius: 10px;
            padding: 5px 10px;
            font-weight: 400;
            font-size: 16px;
            &.disabled {
                background: var(--color-blue-grey);
                color: var(--color-purple);
                border: 1px solid transparent;
                pointer-events: none;
                &::placeholder {
                    color: var(--color-purple);
                }
            }
            &::placeholder {
                color: var(--color-grey2-dlya-dop-teksta);
            }
        }
    }

    // .popup-settings-profile3__buttons

    &__buttons {
        display: flex;
        gap: 20px;
    }
}

