        }
    }

    // .popup-my-interests__name

    &__name {
        flex: 1 1 auto;
        font-weight: 400;
        font-size: 16px;
        line-height: 120%;
        @media (max-width: 550px) {
            font-size: 14px;
        }
    }

    // .popup-my-interests__buttons

    &__buttons {
        flex: 0 0 auto;
        display: flex;
        gap: 50px;
        @media (max-width: 768px) {
            gap: 30px;
        }
        @media (max-width: 550px) {
            gap: 10px;
        }
    }

    // .popup-my-interests__button

    &__button {
        display: flex;
        justify-content: center;
        align-items: center;
        flex: 0 0 34px;
        border-radius: 50%;
        width: 34px;
        height: 34px;
        transition: background-color 0.3s;
        img {
            width: 19px;
            height: 19px;
        }
        &.like {
            &.active {
                background-color: #a3e7ad;
                img {
                    filter: invert(56%) sepia(100%) saturate(4670%) hue-rotate(107deg) brightness(100%) contrast(70%);
                }
            }
        }
        &.nolike {
            img {
                display: block;
                transform: rotate(-180deg);
                margin: 2px 0px 0px 0px;
            }
            &.active {
                background-color: #ff9e99;
                img {
                    filter: invert(44%) sepia(14%) saturate(4518%) hue-rotate(327deg) brightness(86%) contrast(86%);
                }
            }
        }
    }
